.popup {
    font-family: 'Inter', Arial, sans-serif;
    position: fixed;
    z-index: 100;
    color: #000;
    background: rgba(0, 0, 0, .5);
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    visibility: hidden;
    opacity: 0;
    transition: opacity .5s;
}